<template class="text-center">
  <v-row>
    <v-col class="text-center">
      <img alt="Vue logo" width="300" src="@/assets/sl.jpg">
    </v-col>
  </v-row>
  <v-row>
    <v-col cols="6">
      <v-text-field label="Name" v-model="name"></v-text-field>
    </v-col>
    <v-col cols="6">
      <h1 class="text-center">{{ formattedDateTime }}</h1>
    </v-col>
    <v-col cols="6">
      <v-select label="Gender" :items="genders" v-model="gender"></v-select>
    </v-col>
    <v-col cols="6">
      <v-autocomplete
        label="Nationality"
        :items="nationalities"
        v-model="selectedNationality"
        item-text="text"
        item-value="value"
        return-object
        allow-input
        @update:model-value="updateNationalities"
      ></v-autocomplete>
    </v-col>
    <v-col cols="6">
      <v-select label="Number of Visits" :items="visitOptions" v-model="visits"></v-select>
    </v-col>
    <v-col cols="6">
      <v-text-field label="Email" v-model="email"></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field label="Phone number" v-model="phone"></v-text-field>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      gender: '',
      selectedNationality: null,
      visits: 1,
      email: '',
      phone: '',
      genders: ['Male', 'Female', 'Other'],
      visitOptions: [...Array(50).keys()].map(i => i + 1),
      nationalities: [
        "Egyptian",
        "Russian",
        "Ukraine",
      ]
    };
  },
  methods: {
    updateNationalities(newValue) {
      if (!this.nationalities.some(item => item.text === newValue)) {
        this.nationalities.push({ text: newValue, value: newValue });
      }
    }
  },
  computed: {
    formattedDateTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = (now.getMonth() + 1).toString().padStart(2, '0');
      const day = now.getDate().toString().padStart(2, '0');
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');
      return `${year}-${month}-${day} 
      (${hours}:${minutes})`;
    }
  }
  };
</script>