<template>
    <v-row justify="left">
      <v-dialog
      variant="outlined"
      density="default"
        v-model="dialog"
        persistent
      >
        <template v-slot:activator="{ props }">
          
          <v-container>
            <v-row>
              <v-col>
                <v-sheet style="display: flex;">
                  <v-btn variant="outlined" class="ml-1" v-bind="props" append-icon="mdi-account-plus text-green">Add new iPRange</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="update" append-icon="mdi-file-edit-outline text-blue">Update selected iPRange</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="remove"  append-icon="mdi-delete-circle-outline text-red">Delete selected iPRange</v-btn>
                </v-sheet>
              </v-col>
            </v-row>
          </v-container>

        </template>
        <v-card>
          <v-card-title>
            <span class="text-h5">IPRange Profile</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-alert 
                v-if="errorMessage"
                color="error"
                icon="$error"
                title="Error!"
                :text="errorMessage"
              ></v-alert>
              
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field label="IP Address Range" v-model="ip_address_range" hint="Enter the ip address range!" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field label="Scan Every? in minutes" v-model="scan_every" hint="Scan every (x) minutes!" required></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue-darken-1" variant="text" @click="dialog = false" append-icon="mdi-close-box">
              Close
            </v-btn>
            <v-btn color="blue-darken-1" variant="text" append-icon="mdi-zip-disk" :prepend-icon="loading" @click="Save" :disabled="loading !==''">
              {{iPRangeId==""?"Save":"Update"}}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </template>
  <script>
import Axios from '@/Axios';

  export default {
    data: () => ({
      dialog: false,
      ip_address_range: "",
      scan_every:"",
      errorMessage: "",
      loading:"",
      iPRangeId:""
    }),
    watch: {
      dialog(visible) {
        if (!visible ) {
          
          this.ip_address_range = "";
          this.scan_every = "";

          this.errorMessage = "";
          this.iPRangeId = "";

          this.triggerDataTableRequest();
        }
      },
    },
    props: [ 'triggerDataTableRequest','getIPRangeId' ],
    methods:{
      async Save(){
        this.loading = "mdi-timer-sand-complete mdi-spin text-red mdi-24px";
        await Axios.api("iPRanges", "action",{
          ip_address_range:this.ip_address_range,
          scan_every:this.scan_every,


          id:this.iPRangeId,
        }).then(res=>{
          this.loading="";
          if(res.data.message !== "Successful"){
            this.errorMessage=res.data.message;
          }else{
            this.loading="";
            this.dialog=false;
          }

        }).catch(err=>{
          this.loading="";
          this.errorMessage=err.message;
        });
      },
      async remove(){
        await Axios.api("iPRanges", "delete",{
          id:await this.getIPRangeId(),
        }).then(()=>{
          this.triggerDataTableRequest();
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      },
      async update(){
        this.iPRangeId=this.getIPRangeId();
        await Axios.api("iPRanges", "get",{
          id:this.iPRangeId,
        }).then((res)=>{
          if(res.data.ip_address_range){
            const model = res.data;
            this.ip_address_range = model.ip_address_range;
            this.scan_every = model.scan_every;


            this.dialog = true;
          }
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      }
    }
  }
</script>