<template>
    <v-row justify="left">
      <v-dialog
      variant="outlined"
      density="default"
        v-model="dialog"
        persistent
      >
        <template v-slot:activator="{ props }">
          
          <v-container>
            <v-row>
              <v-col>
                <v-sheet style="display: flex;">
                  <v-btn variant="outlined" class="ml-1" v-bind="props" append-icon="mdi-account-plus text-green">Add new user</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="update" append-icon="mdi-file-edit-outline text-blue">Update selected user</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="remove"  append-icon="mdi-delete-circle-outline text-red">Delete selected user</v-btn>
                </v-sheet>
              </v-col>
            </v-row>
          </v-container>

        </template>
        <v-card>
          <v-card-title>
            <span class="text-h5">User Profile</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-alert 
                v-if="errorMessage"
                color="error"
                icon="$error"
                title="Error!"
                :text="errorMessage"
              ></v-alert>
              
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Username" v-model="username" hint="Enter your username!" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Password" v-model="password" hint="Enter your password!" required type="password"></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue-darken-1" variant="text" @click="dialog = false" append-icon="mdi-close-box">
              Close
            </v-btn>
            <v-btn color="blue-darken-1" variant="text" append-icon="mdi-zip-disk" :prepend-icon="loading" @click="Save" :disabled="loading !==''">
              {{userId==""?"Save":"Update"}}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </template>
  <script>
import Axios from '@/Axios';

  export default {
    data: () => ({
      dialog: false,
      username: "",
      password: "",
      errorMessage: "",
      loading:"",
      userId:""
    }),
    watch: {
      dialog(visible) {
        if (!visible ) {
          
          this.username = "";
          this.password = "";
          this.errorMessage = "";
          this.userId = "";

          this.triggerDataTableRequest();
        }
      },
    },
    props: [ 'triggerDataTableRequest','getUserId' ],
    methods:{
      async Save(){
        this.loading = "mdi-timer-sand-complete mdi-spin text-red mdi-24px";
        await Axios.api("users", "action",{
          username:this.username,
          password:this.password,
          id:this.userId,
        }).then(res=>{
          this.loading="";
          if(res.data.message !== "Successful"){
            this.errorMessage=res.data.message;
          }else{
            this.loading="";
            this.dialog=false;
          }

        }).catch(err=>{
          this.loading="";
          this.errorMessage=err.message;
        });
      },
      async remove(){
        await Axios.api("users", "delete",{
          id:await this.getUserId(),
        }).then(()=>{
          this.triggerDataTableRequest();
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      },
      async update(){
        this.userId=this.getUserId();
        await Axios.api("users", "get",{
          id:this.userId,
        }).then((res)=>{
          if(res.data.username){
            const u = res.data;
            this.username = u.username;
            this.dialog = true;
          }
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      }
    }
  }
</script>