import moment from 'moment';
const diffForHumans = (dt)=>{
    return moment.duration(moment().diff(moment(dt))).humanize()
}
const diffForHumansDT = (data)=>{
    data.forEach((element,i) => {
        data[i].createdAt=diffForHumans(element.createdAt);
        data[i].updatedAt=diffForHumans(element.updatedAt);
    });
    return data;
}
export default {
    diffForHumans,diffForHumansDT
}