<template>
    <v-row justify="left">
      <v-dialog
      variant="outlined"
      density="default"
        v-model="dialog"
        persistent
      >
        <template v-slot:activator="{ props }">
          
          <v-container>
            <v-row>
              <v-col>
                <v-sheet style="display: flex;">
                  <v-btn variant="outlined" class="ml-1" v-bind="props" append-icon="mdi-account-plus text-green">Add new device</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="update" append-icon="mdi-file-edit-outline text-blue">Update selected device</v-btn><br />
                  <v-btn variant="outlined" class="ml-1" @click="remove"  append-icon="mdi-delete-circle-outline text-red">Delete selected device</v-btn>
                </v-sheet>
              </v-col>
            </v-row>
          </v-container>

        </template>
        <v-card>
          <v-card-title>
            <span class="text-h5">Device Profile</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-alert 
                v-if="errorMessage"
                color="error"
                icon="$error"
                title="Error!"
                :text="errorMessage"
              ></v-alert>
              
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field label="IP Address" v-model="ip_address" hint="Enter the device ip address!" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Username" v-model="username" hint="Enter the device username!" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Password" v-model="password" hint="Enter the device username!" required></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Room Number" v-model="room_number" hint="Enter the device room number!" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Guest name" v-model="guest_name" hint="Enter the guest name!" required></v-text-field>
                </v-col>

              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue-darken-1" variant="text" @click="dialog = false" append-icon="mdi-close-box">
              Close
            </v-btn>
            <v-btn color="blue-darken-1" variant="text" append-icon="mdi-zip-disk" :prepend-icon="loading" @click="Save" :disabled="loading !==''">
              {{deviceId==""?"Save":"Update"}}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </template>
  <script>
import Axios from '@/Axios';

  export default {
    data: () => ({
      dialog: false,
      ip_address: "",
      username: "",
      password: "",
      room_number: "",
      guest_name: "",
      errorMessage: "",
      loading:"",
      deviceId:""
    }),
    watch: {
      dialog(visible) {
        if (!visible ) {
          
          this.ip_address = "";
          this.username = "";
          this.password = "";
          this.room_number = "";
          this.guest_name = "";


          this.errorMessage = "";
          this.deviceId = "";

          this.triggerDataTableRequest();
        }
      },
    },
    props: [ 'triggerDataTableRequest','getDeviceId' ],
    methods:{
      async Save(){
        this.loading = "mdi-timer-sand-complete mdi-spin text-red mdi-24px";
        await Axios.api("devices", "action",{
          ip_address:this.ip_address,
          guest_name:this.guest_name,
          room_number:this.room_number,
          username:this.username,
          devicename:this.devicename,
          password:this.password,
          id:this.deviceId,
        }).then(res=>{
          this.loading="";
          if(res.data.message !== "Successful"){
            this.errorMessage=res.data.message;
          }else{
            this.loading="";
            this.dialog=false;
          }

        }).catch(err=>{
          this.loading="";
          this.errorMessage=err.message;
        });
      },
      async remove(){
        await Axios.api("devices", "delete",{
          id:await this.getDeviceId(),
        }).then(()=>{
          this.triggerDataTableRequest();
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      },
      async update(){
        this.deviceId=this.getDeviceId();
        await Axios.api("devices", "get",{
          id:this.deviceId,
        }).then((res)=>{
          if(res.data.ip_address){
            const model = res.data;
            this.ip_address = model.ip_address;
            this.guest_name = model.guest_name;
            this.room_number = model.room_number;
            this.username = model.username;
            this.password = model.password;
            this.dialog = true;
          }
        }).catch(err=>{
          this.errorMessage=err.message;
        });
      }
    }
  }
</script>