import { createRouter, createWebHistory } from 'vue-router'
import store from '@/store'

import HomeView from '@/views/HomeView.vue';
import AboutView from '@/views/AboutView.vue';
import MainView from '@/views/Screens/en/MainView.vue';
import SignINView from '@/views/Screens/en/SignINView.vue';
import RateUSView from '@/views/Screens/en/RateUSView.vue';
import LoginView from '@/views/LoginView.vue';

import UserView from '@/views/Users/ModelIndex.vue';
import UserManage from '@/views/Users/ModelManage.vue';

import DeviceView from '@/views/Devices/ModelIndex.vue';
import DeviceManage from '@/views/Devices/ModelManage.vue';

import IPRangeView from '@/views/IPRanges/ModelIndex.vue';
import IPRangeManage from '@/views/IPRanges/ModelManage.vue';

import ProtelView from '@/views/Protel/ModelIndex.vue';


import Axios from '@/Axios';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      requiresAuth: true,
      title: "Home",
    }
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView,
    meta: {
      requiresAuth: true,
      title: "About",
    }
  },
  {
    path: '/main',
    name: 'main',
    component: MainView,
    meta: {
      requiresAuth: true,
      title: "Main",
    }
  },
  {
    path: '/signin',
    name: 'signin',
    component: SignINView,
    meta: {
      requiresAuth: true,
      title: "Sign In",
    }
  },
  {
    path: '/rateus',
    name: 'rateus',
    component: RateUSView,
    meta: {
      requiresAuth: true,
      title: "Rate US",
    }
  },

  
  {
    path: '/Login',
    name: 'Login',
    component: LoginView,
    meta: {
      title: "Login",
    }
    
  },
  {
    path: '/Users',
    name: 'Users',
    component: UserView,
    meta: {
      requiresAuth: true,
      title: "Users",
    }
    
  },
  {
    path: '/Users/Manage',
    name: 'Users/Manage',
    component: UserManage,
    meta: {
      requiresAuth: true,
      title: "Manage",
    }
    
  },
  {
    path: '/Devices',
    name: 'Devices',
    component: DeviceView,
    meta: {
      requiresAuth: true,
      title: "Devices",
    }
    
  },
  {
    path: '/Devices/Manage',
    name: 'Devices/Manage',
    component: DeviceManage,
    meta: {
      requiresAuth: true,
      title: "Manage",
    }
    
  },
  {
    path: '/IPRanges',
    name: 'IPRanges',
    component: IPRangeView,
    meta: {
      requiresAuth: true,
      title: "IPRanges",
    }
    
  },
  {
    path: '/IPRanges/Manage',
    name: 'IPRanges/Manage',
    component: IPRangeManage,
    meta: {
      requiresAuth: true,
      title: "Manage",
    }
    
  },
  {
    path: '/Protel',
    name: 'Protel',
    component: ProtelView,
    meta: {
      requiresAuth: true,
      title: "Protel",
    }
    
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach( async (to, from, next) => {
  document.title=`${to.meta.title}`;
  
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    const token = localStorage.getItem("token");
    if(token){
      //Verify
      await Axios.verify().then(()=>{
        store.state.loggedIn=true;
      }).catch(err=>{
        console.log(err);
      });

    }
    
    if (!store.getters.isLoggedIn) {
      next({ name: 'Login' })
    } else {
      next() // go to wherever I'm going
    }
  } else {
    if(to.name === "Login" && store.getters.isLoggedIn){
      next({ name: 'home' });
    }else{
      next() // does not require auth, make sure to always call next()!
    }
  }
})
export default router
