
const axios = require('axios').default
const server_ip = "127.0.0.1";
const port = "5038";
// const apiRoot="/api/";


let login =  async (req)=>{
  return await axios({
     method: 'post',
     url: `http://${server_ip}:${port}/login`,
     data:{
       username: req.username,
       password:  req.password,
     }
   }).then(res => {
     return res;
   });
}


let verify =  async ()=>{
  return await axios({
     method: 'post',
     url: `http://${server_ip}:${port}/verify`,
     headers:{
       'Authorization':'Bearer ' + localStorage.getItem("token")
     }
   }).then(res => {
     return res;
   });
}
let api = async (model, method, data)=>{
  return await axios({
    method: 'post',
    url: `http://${server_ip}:${port}/api/${model}/${method}`,
    headers:{
      'Authorization':'Bearer ' + localStorage.getItem("token")
    },
    data:data
  }).then(res => {
    return res;
  });
}

export default {
    login, verify, api
}