<template>
  <h1>Protel In House</h1>

  <v-btn variant="outlined" class="ml-1" @click="getDataTable()" append-icon="mdi-database-arrow-down-outline">Refresh</v-btn><br />

  <v-btn variant="outlined" class="ml-1" @click="updateDevicesAndGetNames()" append-icon="mdi-auto-fix">Quck Fix</v-btn><br />

  <v-row>
    <v-col cols="auto"><h3 class="text-success bg-black">Good Devices: {{GoodDevices}} <v-icon icon="mdi-hand-okay"></v-icon></h3></v-col>
  </v-row>
  <v-row>
    <v-col cols="auto"><h3 class="text-orange bg-black">Troubled Devices: {{ErrorDevices}} <v-icon icon="mdi-close-box-outline"></v-icon></h3></v-col>
    <v-col cols="auto"><h3 class="text-yellow bg-black">Not connected devices: {{ErrorNotConnected}} <v-icon icon="mdi-power-plug-off-outline"></v-icon></h3></v-col>
    <v-col cols="auto"><h3 class="text-red bg-black">Device Name Mismatch: {{ErrorNameMismatch}} <v-icon icon="mdi-skull-crossbones-outline"></v-icon></h3></v-col>
    
  </v-row>
  <v-sheet class="pa-3">
    <br />
    <DataTable 
      :columns="columns"
      :data="data" 
      class="display"
      ref="table"
    >
  
    </DataTable>
  </v-sheet>
</template>
<script setup>
  import DataTable from 'datatables.net-vue3';
  import DataTablesCore from 'datatables.net';
  import Axios from '@/Axios';
  import { ref } from 'vue';
  DataTable.use(DataTablesCore);
  let data=ref([]);
  let columns=[
    { "data": "guest_name", "title": "Guest Name"},
    { "data": "room_number", "title": "Room Number"},
    { "data": "device_guest_name", "title": "Name on the device"},
    { "data": "room_error", "title": "status"},
  ];
  let GoodDevices=ref(0);
  let ErrorDevices=ref(0);
  let ErrorNotConnected=ref(0);
  let ErrorNameMismatch=ref(0);

  async function updateDevicesAndGetNames(){
    await Axios.api("Protel", "updateDevicesAndGetNames", []).then(res=>{
      console.log(res);
      getDataTable();
    });
  }

  async function getDataTable(){
    data.value=[];
    await Axios.api("Protel", "", []).then(res=>{
       data.value=res.data;
    });
    GoodDevices.value=0;
    ErrorDevices.value=0;
    
    ErrorNotConnected.value=0;
    ErrorNameMismatch.value=0;

    data.value.forEach(element => {
      if(element.room_error)
        GoodDevices.value++;
      else
        ErrorDevices.value++;
        if(element.device_guest_name !== element.guest_name && element.device_guest_name !== "") ErrorNameMismatch.value++;
        if(element.device_guest_name == "") ErrorNotConnected.value++;
    });
  }
  getDataTable()



</script>
<style>
@import 'datatables.net-dt';
</style>