<template>
  <v-row>
    <v-col class="text-center">
      <img alt="Vue logo" width = "300" src="@/assets/sl.jpg">
    </v-col>
  </v-row>
  <v-row>
    <v-col cols="12">
      <h2 class="text-center">Rating</h2>
    </v-col>
  </v-row>

  <v-row class="mb-15">
    <v-col cols="12" sm="4" v-for="(item, index) in items" :key="index">
      <h3 class="text-center">{{ item.title }}</h3>
      <v-rating
        v-model="item.rating"
        color="yellow"
        dense
        half-increments
        size="32"
      ></v-rating>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { title: 'Cleanliness', rating: 0 },
        { title: 'Reception', rating: 0 },
        { title: 'Team', rating: 0 }
      ]
    };
  },
  computed: {
    formattedDateTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = (now.getMonth() + 1).toString().padStart(2, '0');
      const day = now.getDate().toString().padStart(2, '0');
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');

      return `${year}-${month}-${day} (${hours}:${minutes})`;
    }
  }
};
</script>
<style>
.v-rating {
  display: flex;
  justify-content: center;
}
</style>