<template>
    <div class="d-flex align-center justify-center" style="height: 100vh">
        <v-sheet width="400" class="mx-auto">
            <img alt="Vue logo" src="@/assets/sl.jpg">
            <h3>GRANDSTREAM VOIP MANAGER</h3>
            <br />
            <v-alert 
              v-if="errorMessage"
              color="error"
              icon="$error"
              title="Error!"
              :text="errorMessage"
            ></v-alert>
            <v-form fast-fail @submit.prevent="handleLogin">
                <v-text-field v-model="username" label="User Name" :rules="rules"></v-text-field>
                <v-text-field v-model="password" label="password" type="password" :rules="rules"></v-text-field>
                <v-btn color="blue"  block class="mt-2" @click="handleLogin">Sign in</v-btn>
            </v-form>
        </v-sheet>
    </div>
</template>

<script>
import Axios from '@/Axios';

  export default {
    data: () => ({
      username: "AmirFox",
      password:"Fox@@262",
      errorMessage: "",
      rules: [
        value => {
          if (value) return true
          return 'Please enter something!'
        },
      ],

    }),
    methods: {
      handleLogin(){
        Axios.login({
          username:this.username,
          password:this.password,
        }).then((res)=>{
          if(res.data.token){
            res.data=res.data.token;
            const expiryData=JSON.parse(atob(res.data.split('.')[1]));
            //Logout after token expires!
            setTimeout(() => {
              this.$store.dispatch('logout');
            }, (expiryData.exp - expiryData.iat) * 1000);

            const token=res.data;
            this.$store.dispatch('login', {token:token});
          }
          if(res.error){
            this.errorMessage=res.error;
          }
        }).catch(err=>{
          this.errorMessage=err.message;
        })
      }
    },
  }
</script>