<template>
  <h1>IPRanges</h1>
  <br />
  
  <v-sheet class="pa-3">
    <ModelManage  
    :triggerDataTableRequest="getDataTable"
    :getIPRangeId="setIPRangeId"
    :zanagiro="zanagiro"
    />
  
    <br />
    <DataTable 
      :columns="columns"
      :data="data" 
      class="display"
      :options="{select: {info:false, style:'single'}}"
      ref="table"
    >
  
    </DataTable>
  </v-sheet>
</template>
<script setup>
  import DataTable from 'datatables.net-vue3';
  import DataTablesCore from 'datatables.net';
  import DTHelpers from '@/helpers/DataTable';
  import Axios from '@/Axios';
  import { onMounted, ref } from 'vue';
  import ModelManage from './ModelManage.vue';
  import Select from 'datatables.net-select';
  DataTable.use(DataTablesCore);
  DataTable.use(Select);
  let data=ref([]);
  let columns=[
    { "data": "_id", "title": "ID", "visible":false},
    { "data": "ip_address_range", "title": "ip_address_range"},
    { "data": "scan_every", "title": "scan_every"},
    { "data": "createdAt", "title": "Created At"},
    { "data": "updatedAt", "title": "Updated At"},
  ]
  async function getDataTable(){
    await Axios.api("iPRanges", "", []).then(res=>{
      data.value=DTHelpers.diffForHumansDT(res.data);
    });
  }

  let dt;
  const table = ref();
  function setIPRangeId(){
    let iPRangeId=""
    dt.rows({selected: true}).every(function(){
      iPRangeId=this.data()._id;
    });
    return iPRangeId;
  }
  let zanagiro="OK";

  onMounted( function(){
    dt = table.value.dt;
    getDataTable();
    dt.on("select deselect", function(){
      setIPRangeId();
    });
  });

</script>
<style>
@import 'datatables.net-dt';
</style>