import { createStore } from 'vuex'
import router from '@/router';
export default createStore({
  state: {
    sideDrawer: false,
    loggedIn: true,
    listItems: [
      { title: "Home", icon:"mdi-home", value:"home" , to: "/" },
      { title: "About", icon:"mdi-exclamation-thick", value:"About" , to: "/About" },
      { title: "Main", icon:"mdi-exclamation-thick", value:"Main" , to: "/Main" },
      { title: "SignIN", icon:"mdi-exclamation-thick", value:"SignIN" , to: "/SignIN" },
      { title: "RateUS", icon:"mdi-exclamation-thick", value:"RateUS" , to: "/RateUS" },
    ],
  },
  getters: {
    isLoggedIn(state){
      return state.loggedIn;
    }
  },
  mutations: {
    login(state, token){
      state.token=token;
      localStorage.setItem("token", token);
      state.loggedIn = true;
      router.push("/");
    }
  },
  actions: {
    async login({ commit },{token}){
      commit('login', token);    
    },
    logout({ state }){
      state.loggedIn = false;
      localStorage.removeItem("token");
      router.push("/Login");
    }
  },
  modules: {
  }
})
