<template>
  <h1>Devices</h1>
  <br />
  
  <v-sheet class="pa-3">
    <v-row>
      <v-col cols="auto">
        <v-btn variant="outlined" @click="deleteAllDevices"  class="ml-1" size="x-large" append-icon="mdi-delete-outline text-red">Delete All Devices</v-btn><br />
      </v-col>
      <v-col cols="auto">
        <v-btn variant="outlined" @click="scanAllDevices" :disabled="scanAllLoading !== ''" :prepend-icon="scanAllLoading" class="ml-1" size="x-large" append-icon="mdi-radar text-brown">Scan All IP Ranges</v-btn><br />
      </v-col>
      <v-col cols="auto">
        <v-btn variant="outlined" @click="getDataTable" class="ml-1" size="x-large" append-icon="mdi-refresh-circle text-green">Refresh Devices</v-btn><br />
      </v-col>
    </v-row>
    <ModelManage  
    :triggerDataTableRequest="getDataTable"
    :getDeviceId="setDeviceId"
    :zanagiro="zanagiro"
    />
    <br />

    <v-row>
      <v-col cols="auto"><h3 class="text-info">Total Devices: {{data.length}} </h3></v-col>
    </v-row>
    
    <DataTable 
      :columns="columns"
      :data="data" 
      class="display"
      :options="{select: {info:false, style:'single'}}"
      ref="table"
    >
  
    </DataTable>
  </v-sheet>
</template>
<script setup>
  import DataTable from 'datatables.net-vue3';
  import DataTablesCore from 'datatables.net';
  import DTHelpers from '@/helpers/DataTable';
  import Axios from '@/Axios';
  import { onMounted, ref } from 'vue';
  import ModelManage from './ModelManage.vue';
  import Select from 'datatables.net-select';
  DataTable.use(DataTablesCore);
  DataTable.use(Select);
  let data=ref([]);
  

  let columns=[
    { "data": "_id", "title": "ID", "visible":false},
    { "data": "ip_address", "title": "ip_address"},
    { "data": "room_number", "title": "room_number"},
    { "data": "guest_name", "title": "guest_name"},
    { "data": "username", "title": "username"},
    { "data": "createdAt", "title": "Created At"},
    { "data": "updatedAt", "title": "Updated At"},
  ]
  async function getDataTable(){
    data.value=[];
    await Axios.api("devices", "", []).then(res=>{
      data.value=DTHelpers.diffForHumansDT(res.data);
    });
  }

  let dt;
  const table = ref();
  function setDeviceId(){
    let deviceId=""
    dt.rows({selected: true}).every(function(){
      deviceId=this.data()._id;
    });
    return deviceId;
  }
  let zanagiro="OK";
  const deleteAllDevices = async ()=>{
    await Axios.api("devices", "deleteAll", []).then(()=>{
      getDataTable();
    });
  }
  let scanAllLoading=ref('');
  const scanAllDevices = async ()=>{
    scanAllLoading.value = "mdi-timer-sand-complete mdi-spin text-red mdi-24px";

    await Axios.api("devices", "scanAll", []).then(()=>{
      getDataTable();
      setTimeout(() => {
        getDataTable();
        scanAllLoading.value='';
      }, 25000);
    });
  }
  onMounted( function(){
    dt = table.value.dt;
    getDataTable();
    dt.on("select deselect", function(){
      setDeviceId();
    });
  });

</script>
<style>
@import 'datatables.net-dt';
</style>